import { inject } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { SelectButtonDialogComponent } from "../../../shared/components/molecules/select-button-dialog/select-button-dialog.component";
import { MatBottomSheet } from "@angular/material/bottom-sheet";
import { BottomSheetComponent } from "../../../shared/components/molecules/bottom-sheet/bottom-sheet.component";
import { LogicSketchflowService } from "../../../core/services/logic-sketchflow.service";
import { TextComponent } from "../../chatflow/components/atom/text/text.component";
import { FactoryService } from "../../../core/services/factory.service";

export interface ILayoutAction {
  modalLayout(): void;
  bottomSheetLayout(): void;
  sidenavLeftLayout(): void;
  sidenavRightLayout(): void;
}


export abstract class LayoutAction implements ILayoutAction {

  protected readonly dialog = inject(MatDialog);
  protected readonly bottomSheet = inject(MatBottomSheet);
  private readonly logicSketchflowService = inject(LogicSketchflowService);
  private readonly factoryService = inject(FactoryService);

  public modalLayout() {
    const dialogRef = this.dialog.open(SelectButtonDialogComponent, {
      data: {
        title: 'Modal Layout will pop up like this.',
        content: '',
        items: [],
        btnNo: '닫기'
      }
    });
  }

  public bottomSheetLayout() {
    this.bottomSheet.open(BottomSheetComponent, {
      data: {
        title: 'Bottom Sheet will pop up like this.',
        content: `Bottom Sheet Content --- Bottom Sheet Content --- Bottom Sheet Content`
      }
    });
  }

  public sidenavLeftLayout() {
    // <app-row-detail></app-row-detail>
    /**
     * this.logicSketchflowService.rowDetailContainer가 null이 아니면, 
     * 
     */
    this.logicSketchflowService.rowDetailContainer.clear();
    this.logicSketchflowService.rowInputComponent = null;
    this.factoryService.createComponent(
      TextComponent,
      this.logicSketchflowService.rowDetailContainer,
      {
        text: 'Sidenav Left will pop up like this.',
        position: {
          x: 10,
          y: 10
        },
        font: {
          weight: 100,
          size: 'large'
        }
      }
    );
    this.logicSketchflowService.sketchflowDrawerPosition = 'start';
    this.logicSketchflowService.sketchflowDrawer.toggle();

  }

  public sidenavRightLayout() {
    this.logicSketchflowService.rowDetailContainer.clear();
    this.logicSketchflowService.rowInputComponent = null;
    this.factoryService.createComponent(
      TextComponent,
      this.logicSketchflowService.rowDetailContainer,
      {
        text: 'Sidenav Right will pop up like this.',
        position: {
          x: 10,
          y: 10
        },
        font: {
          weight: 100,
          size: 'large'
        }
      }
    );
    this.logicSketchflowService.sketchflowDrawerPosition = 'end';
    this.logicSketchflowService.sketchflowDrawer.toggle();
  }

}