
export interface ISketchflowLayoutComponent {
  key: string;
  value: {
    name: string;
    data: any | null;
  }
}

export class SketchflowLayoutComponentConst {

  public components: ISketchflowLayoutComponent[] = [
    {
      key: 'input',
      value: {
        name: '입력',
        data: null
      }
    },
    {
      key: 'button',
      value: {
        name: '버튼',
        data: [
          {
            itemName: '',
            icon: '',
            color: '',
            scale: ''
          }
        ]
      }
    },
    {
      key: 'text',
      value: {
        name: '글',
        data: ''
      }
    },
  ]

  public isContain(key: string): boolean { 
    return this.components.some(layout => layout.key === key);
  }

}