
export interface ISketchflowLayout {
  key: string;
  value: {
    name: string;
  }
}

export class SketchflowLayoutActionConst {
  
  public actions: ISketchflowLayout[] = [
    {
      key: 'modal',
      value: {
        name: '모달'
      }
    },
    {
      key: 'bottomSheet',
      value: {
        name: '바텀 시트'
      }
    },
    {
      key: 'sidenav-left',
      value: {
        name: '사이드 왼쪽'
      }
    },
    {
      key: 'sidenav-right',
      value: {
        name: '사이드 오른쪽'
      }
    },
    {
      key: 'page',
      value: {
        name: '페이지'
      }
    }
  ]

  public isContain(key: string): boolean {
    return this.actions.some(layout => layout.key === key);
  }

}