import { Component, ComponentRef, OnDestroy, OnInit, inject } from '@angular/core';
import { DataAbstract } from '../../../../core/classes/data.abstract';
import { IValue } from '../../classes/data/sketchflow.data';
import { RowInputComponent } from '../row-input/row-input.component';
import { BoxComponent } from '../box/box.component';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-row',
  templateUrl: './row.component.html',
  styleUrl: './row.component.scss'
})
export class RowComponent extends DataAbstract implements OnInit, OnDestroy {

  private readonly router = inject(Router);
  private readonly route = inject(ActivatedRoute);

   // 컴포넌트 이름
   public id: string = '';
   public type = 'row';
   public name: string = '';
   public value: string = '';

   // 위치 속성
   public width!: string;
   public height!: string;
   public x!: number;
   public y!: number;
 
   // 레이아웃 col
   public colSpan!: number;
   public boxs: IValue[] = [];

   public boxComponents: ComponentRef<BoxComponent>[] = [];

   public isSelected: boolean = false;
   private component!: ComponentRef<BoxComponent>;
   
   public override dataInit(): void {
     if (this.data.id) this.id = this.data.id;
     if (this.data.type) this.type = this.data.type;
     if (this.data.name) this.name = this.data.name;
     if (this.data.value) this.value = this.data.value;
 
     if (this.data?.size?.width) this.width = this.data.size.width;
     if (this.data?.size?.height)this.height = this.data.size.height;
     if (this.data?.position?.x) this.x = this.data.position.x;
     if (this.data?.position?.y) this.y = this.data.position.y;
     if (this.data?.boxs) this.boxs = [...this.data.boxs];
 
     if (this.data?.cell?.colSpan) this.colSpan = this.data.cell.colSpan;
   }
 
   ngOnInit(): void {
    console.log('row component OnInit');
     this.route.queryParams.subscribe(params => {
      const selectedId = params['rowId'];
      const boxId = params['boxId'];
      if (boxId) {
        this.isSelected = false;
        return;
      }
      this.isSelected = false;
      if (this.id === selectedId) this.isSelected = true;
     });

     // 여기서부터는 box 컴포넌트를 그릴거야.
     const rowInstance = this.logicSketchflowService.rows.find(row => row.instance.id === this.id)?.instance;
     if (!rowInstance) return;
    
     if (this.boxs.isEmpty()) return;
     this.boxComponents = [];

     this.boxs.forEach((box: IValue, index: number) => {
      box.size.width = this.layoutService.convertStarWidth(this.layoutService.calculateWidth(this.width, this.x), this.colSpan);
      this.component = this.factoryService.createComponent(BoxComponent, rowInstance.container, box);
      this.component.instance.parentId = this.id;
      this.boxComponents.push(this.component);
     });
   }

   public onClickDetail() {
    console.log('click row');
    this.logicSketchflowService.sketchflowDrawerPosition = 'end';
    this.logicSketchflowService.sketchflowDrawer.toggle();
    this.router.navigate([`/sketchflow/${this.logicSketchflowService.currentPage}`], {
      queryParams: {
        rowId: this.id
      }
    });
 
    const rowData: IValue = {
      id: this.id,
      type: this.type as 'row',
      name: this.name,
      value: this.value,
      size: {
        width: this.width,
        height: this.height
      },
      position: {
         x: this.x,
         y: this.y,
      },
      cell: {
         colSpan: this.colSpan
      },
      path: '',
      boxs: this.boxs
    }

    if (this.logicSketchflowService.rowInputComponent == null) {
      this.logicSketchflowService.rowDetailContainer.clear();
      this.logicSketchflowService.rowInputComponent = this.factoryService.createComponent(
        RowInputComponent,
        this.logicSketchflowService.rowDetailContainer,
        rowData
      );
    }

    this.logicSketchflowService.rowInputComponent.instance.data = rowData;
   }

   ngOnDestroy(): void {
    console.log('row component OnDestroy');
    
    // DB에서 삭제시켜야함
    const index = this.logicSketchflowService.rows.findIndex(ref => ref.instance.id === this.id);
    if (index !== -1) {
      this.logicSketchflowService.rows.splice(index, 1);
    }
  }

}
