import { Component, Input, OnDestroy, OnInit, inject } from '@angular/core';
import { DataAbstract } from '../../../../core/classes/data.abstract';
import { ActivatedRoute, Router } from '@angular/router';
import { RowInputComponent } from '../row-input/row-input.component';
import { IValue } from '../../classes/data/sketchflow.data';
import { MatDialog } from '@angular/material/dialog';
import { SelectButtonDialogComponent } from '../../../../shared/components/molecules/select-button-dialog/select-button-dialog.component';
import { SketchflowLayoutActionConst } from '../../classes/data/sketchflow.action.const';
import { SketchflowLayoutAction } from '../../classes/sketchflow.layout.action';
import { SketchflowLayoutComponentConst } from '../../classes/data/sketchflow.component.const';

@Component({
  selector: 'app-box',
  templateUrl: './box.component.html',
  styleUrl: './box.component.scss'
})
export class BoxComponent extends DataAbstract implements OnInit, OnDestroy {

  protected readonly dialog = inject(MatDialog);

  @Input() parentId!: string; 

  private readonly router = inject(Router);
  private readonly route = inject(ActivatedRoute);
  
  // 컴포넌트 이름
  public id: string = '';
  public type: string = 'box';
  public name: string = '';
  public value: string = '';

  // 위치 속성
  public width!: string;
  public height!: string;
  public x!: number;
  public y!: number;

  // 레이아웃 col
  public colSpan!: number;
  
  public path: string | null = null;

  private maxWidth!: number;
  private maxHeight!: number;
  public isSelected: boolean = false;

  // public layoutActionConst = new SketchflowLayoutActionConst();
  public sketchflowLayoutAction = new SketchflowLayoutAction();
  public layoutComponentConst = new SketchflowLayoutComponentConst();

  public override dataInit(): void {
     if (this.data.id) this.id = this.data.id;
     if (this.data.type) this.type = this.data.type;
     if (this.data.name) this.name = this.data.name;
     if (this.data.value) this.value = this.data.value;
 
     if (this.data?.size?.width) {
      this.width = this.data.size.width;
      this.maxWidth = this.layoutService.convertStringToNumber(this.data.size.width); // box의 최대 width, height 정의
     }
     if (this.data?.size?.height) {
      this.height = this.data.size.height;
      this.maxHeight = this.layoutService.convertStringToNumber(this.data.size.height); // box의 최대 width, height 정의
     }
     if (this.data?.position?.x) this.x = this.data.position.x;
     if (this.data?.position?.y) this.y = this.data.position.y;
 
     if (this.data?.cell?.colSpan) this.colSpan = this.data.cell.colSpan;
     if (this.data.path) this.path = this.data.path;
  }

  ngOnInit(): void {
    console.log('box component OnInit');
    this.route.queryParams.subscribe(params => {
      const selectedId = params['boxId'];
      this.isSelected = false;
      if (this.id === selectedId) this.isSelected = true;
    });
  }

  public onClickBox(event: MouseEvent) {
    event.stopPropagation();
    console.log('click box');

    this.router.navigate([`/sketchflow/${this.logicSketchflowService.currentPage}`], {
      queryParams: {
        rowId: this.parentId,
        boxId: this.id,
      }
     });
    
    // 모달 띄워서 box 선택 시, 어떤 걸 할지?
    // 디테일 변경, 컴포넌트 액션 (라우터 이동, 해당 컴포넌트 레이아웃 변경)
    const dialogRef = this.dialog.open(SelectButtonDialogComponent, {
      data: {
        title: 'Select your next step',
        content: '',
        items: ['Change Detail', 'Component Action'],
        btnNo: '닫기'
      }
    })

    dialogRef.afterClosed().subscribe(d => {
      if (d == null || d.close === false) {
        return;
      }

      if (d.data === 'Change Detail') {
        this.logicSketchflowService.sketchflowDrawerPosition = 'end';
         this.logicSketchflowService.sketchflowDrawer.toggle();
         const boxData: IValue = {
           id: this.id,
           type: this.type as 'box',
           name: this.name,
           value: this.value,
           size: {
             width: this.width,
             height: this.height
           },
           position: {
             x: this.x,
             y: this.y,
           },
           cell: {
             colSpan: this.colSpan
           },
           path: '',
           boxs: []
         }

        if (this.logicSketchflowService.rowInputComponent == null) {
          this.logicSketchflowService.rowDetailContainer.clear();
          this.logicSketchflowService.rowInputComponent = this.factoryService.createComponent(
            RowInputComponent,
            this.logicSketchflowService.rowDetailContainer,
            boxData
          );
        }

        this.logicSketchflowService.rowInputComponent.instance.data = boxData;
        return;
      }

      if (d.data === 'Component Action') {
        if (this.path == null && this.name === '') {
          return;
        }

        switch (this.name) {
          case 'page':
            this.router.navigateByUrl(`/sketchflow/${this.path}`);
            break;
          // 컴포넌트 레이아웃 액션 발생
          case 'modal':
            this.sketchflowLayoutAction.modalLayout();
            break;
          case 'bottomSheet':
            this.sketchflowLayoutAction.bottomSheetLayout();
            break;
          case 'sidenav-left':
            // 왼쪽 사이드 네브
            this.sketchflowLayoutAction.sidenavLeftLayout();
            break;
          case 'sidenav-right':
            // 오른쪽 사이드 네브
            this.sketchflowLayoutAction.sidenavRightLayout();
            break;
        }
      }
    });
  }

  ngOnDestroy(): void {
    console.log('box component OnDestroy');
  }

  public stringify(obj: any) {
    return JSON.stringify(obj, null, 2);
  }
  
}
