import { Component, OnInit } from '@angular/core';
import { DataAbstract } from '../../../../core/classes/data.abstract';

@Component({
  selector: 'app-row-detail',
  templateUrl: './row-detail.component.html',
  styleUrl: './row-detail.component.scss'
})
export class RowDetailComponent extends DataAbstract implements OnInit {

  ngOnInit(): void {
    this.logicSketchflowService.rowDetailContainer = this.container;
  }

  public onClickClose() {    
    this.logicSketchflowService.sketchflowDrawer.close();
  }

}
